













import {createComponent, reactive} from '@vue/composition-api';
import useTodos from './Store';

const Todo = createComponent({
  setup() {
    const {addTodo, todos} = useTodos();
    const state = reactive({
      todo: '',
    });
    const onSubmit = () => {
      addTodo(state.todo);
      state.todo = '';
    };
    return {
      state,
      onSubmit,
      todos,
    };
  },
});

export default Todo;
