











import Vue, {PropType} from 'vue';

export default Vue.extend({
  name: 'TodoList',
  props: {
    todos: {
      type: Array as PropType<string[]>,
      required: true,
    },
    delete: {
      type: Function,
      required: true,
    },
  },
  methods: {
    onDelete(index: number) {
      this.delete(index);
    },
  },
});
